.news-list {
  padding: 0;
  margin: 0;
  list-style: none;
  --news-pad: 40px;
}
.news-list li {
  transition: background-color 120ms ease, color 120ms ease;
  line-height: 26px;
  margin: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 3px var(--news-pad);
}
.news-list li:hover {
  background-color: #345477;
  color: #f8f8f8;
}
.news-list a {
  display: inline-block;
  padding: 2px 4px;
  transition: color 120ms ease, text-decoration-color 120ms ease, background-color 120ms ease;
}
.news-list li:hover a {
  color: #f8f8f8;
  text-decoration: none;
}
.news-list li:hover img {
  filter: brightness(0) invert(1);
}
.news-panel {
  display: flex;
  flex-direction: column;
  min-height: 0;
}
.loader-bar {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 14px;
  pointer-events: none;
  z-index: 2;
  background-image: repeating-linear-gradient(
      45deg,
      rgba(0, 0, 0, 0.08) 0,
      rgba(0, 0, 0, 0.08) 1px,
      transparent 1px,
      transparent 4px
    ),
    linear-gradient(90deg, #345477 0%, #d45b5b 50%, #345477 100%);
  background-size: 8px 8px, 200% 100%;
  background-position: 0 0, 0 0;
  background-repeat: repeat, repeat;
  mix-blend-mode: multiply;
  animation: loader-pan 3s linear infinite;
}
.loader-bar--overlay {
  left: 0;
  right: 0;
  bottom: 0;
  top: auto;
}
.loader-bar--global {
  position: relative;
  width: 100%;
  pointer-events: none;
}
@keyframes loader-pan {
  0% {
    background-position: 0 0, 0 0;
  }
  100% {
    background-position: 0 0, -200% 0;
  }
}
.news-list-scroll {
  flex: 1;
  min-height: 0;
  overflow-y: auto;
  overflow-x: hidden;
}
@media (max-width: 768px) {
  .news-list {
    --news-pad: 28px;
  }
}
.join-btn {
  transition: background-color 140ms ease, color 140ms ease, border-color 140ms ease;
}
.join-btn:hover {
  background-color: var(--color-void-ivory);
  color: var(--color-void-blue);
  border-color: var(--color-void-blue);
  border-width: 2px;
}
.news-title-dot {
  display: inline-block;
  width: 7px;
  height: 7px;
  background-color: currentColor;
  border-radius: 9999px;
  margin-left: 4px;
  animation: news-dot-blink 1.2s ease-in-out infinite;
}
@keyframes news-dot-blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80%,
  100% {
    opacity: 0;
  }
}
